<script>
    export default {
        name: 'adc-table-column',
        props: {
            prop: { // 
                type: String,
                default: ''
            },
            name: { // 
                type: String,
                default: ''
            },
            width: { // 
                type: [Number, String],
                default: ''
            },
            vm: { // 
                type: Object,
                default() {
                    return {
                        name: '',
                        config: {}
                    };
                }
            },
            type: { // ('selection')
                type: String,
                default: ''
            },
            visible: { //  
                type: [Boolean, Function],
                default: true
            },
            mapper: { // 
                type: Array,
                default() {
                    return [];
                }
            },
            filter: { // (Vuefilter)
                type: [String, Function],
                default: ''
            },
            asHtml: { // html
                type: Boolean,
                default: false
            },
            color: {
                type: Array,
                default() {
                    return [];
                }
            },
            className: {
                type: [String, Function, Array],
                default: ''
            }
        },
        computed: {
            column() {
                return {
                    type: this.type,
                    prop: this.prop,
                    name: this.name,
                    width: this.width,
                    vm: this.vm,
                    visible: this.visible,
                    filter: this.filter,
                    mapper: this.mapper,
                    asHtml: this.asHtml,
                    color: this.color,
                    className: this.className
                };
            }
        },
        render(h) {
            return h('div');
        },
        created() {
            this.addCol();
        },
        methods: {
            addCol() {
                this.$parent.addCol(this.column);
            },
            updateCol() {
                this.$parent.updateCol(this.column);
            }
        },
        watch: {
            column() {
                this.updateCol();
            }
        }
    };
</script>



